                    <CardDescription className="text-blue-100 text-sm font-medium pb-2">ÿ¢ÿÆÿ± 5 ÿ•ÿØÿÆÿßŸÑÿßÿ™ ŸäŸàŸÖŸäÿ©</CardDescription>
